@import "./variables.css";

body {
  color: var(--ec-color-gray);
  font-family:
    system-ui,
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    Roboto,
    Oxygen,
    Ubuntu,
    Cantarell,
    "Open Sans",
    "Helvetica Neue",
    sans-serif;
  margin: 0;
  padding: 0;

  * {
    box-sizing: border-box;
  }
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
}

img {
  max-width: 100%;
}

blockquote {
  border-radius: 6px;
  box-shadow:
    rgba(0, 0, 0, 0.12) 0px 1px 3px,
    rgba(0, 0, 0, 0.24) 0px 1px 2px;
  font-size: 1.5em;
  padding: 1em;
  background-color: #ffffff;
}

.main {
  max-height: 100vh;
  padding: 1em;
}

.overlay {
  filter: blur(1px);
  overflow: auto;
}
